<template>
  <div class="about container py-5">
    <div class="row">
      <div class="about-image col-md-6 col-sm-12 img" @click="destroyRellax">
        <div id="img" class="img-bg">
          <div class="img-bg1" v-rellax="rellax">
            <img src="../../assets/images/demo-0147071367.png" alt="" />
          </div>
          <div class="img-bg2" v-rellax="rellax">
            <img src="../../assets/images/demo-0111010712.jpg" alt="" />
          </div>
          <div class="img-bg3" v-rellax="rellax">
            <img src="../../assets/images/demo-0147626965.png" alt="" />
          </div>
          <div class="img-bg4" v-rellax="rellax">
            <img src="../../assets/images/demo-0111777841.jpg" alt="" />
          </div>
          <div v-rellax="rellax" class="img-name">
            <h4>Louis Mckay</h4>
            <h1>"CHIEF"</h1>
          </div>
        </div>
      </div>

      <div class="carousel-about col-md-6 col-sm-12">
        <h2 class="passion-head px-3 pt-1 mb-5">We serve passion</h2>
        <carousel
          :loop="true"
          :autoplay="true"
          :autoplayTimeout="4000"
          :perPageCustom="[[300, 1], [(350, 1)], [592, 1], [768, 1], [1024, 2]]"
          :paginationEnabled="false"
          :navigationEnabled="true"
          :navigationClickTargetSize="15"
        >
          <slide v-for="item in filterProducts" :key="item.id">
            <product-card :items="item"></product-card>
          </slide>
        </carousel>
      </div>
    </div>
  </div>
</template>

<script>
import ProductCard from "@/components/home/ProductCard";
import ProductPreview from "@/components/home/ProductPreview";
import "../../assets/css/About.css";
export default {
  data() {
    return {
      rellax: {
        speed: -1,
        center: false,
      },
      burger: true,
      pizza: false,
      sushi: false,
      noodles: false,
      name: "pizza",
      category: "all",
      activePriceBurger: "standard",
      test: null,
    };
  },
  components: {
    ProductCard,
    ProductPreview,
  },
  computed: {
    products() {
      return this.$store.state.products;
    },
    filterProducts: function () {
      return this.filterProductsByName(this.filterProductsByCategory(this.products));
    },
  },
  methods: {
    productName: function (v) {
      this.name = v;
      this.category = "all";
    },
    productCatagery: function (v) {
      this.category = v;
    },

    filterProductsByCategory: function (products) {
      return products.filter((product) => product.category.indexOf(this.category) >= 0);
    },

    filterProductsByName: function (products) {
      return products.filter((product) => !product.name.indexOf(this.name));
    },
    sendProductSelect(v) {
      this.$store.commit("changeProductSelect", v);
    },

    destroyRellax() {
      // console.log(window.scrollY);
      if (window.scrollY > 1500) {
        this.rellax = false;
      } else {
        this.rellax = true;
      }
    },
  },
  created() {
    window.addEventListener("scroll", this.destroyRellax);
  },
};
</script>

<style lang="scss" scoped>
.about {
  .about-image {
    .img-bg {
      overflow: hidden;
      position: relative;
      height: 650px;

      .img-bg1 {
        position: absolute;
        left: 5%;
        top: 0;
        z-index: 4;
        transition: 7s;
      }
      .img-bg2 {
        position: absolute;
        top: 180px;
        left: 5%;
        z-index: 3;
        transition: 7s;
      }
      .img-bg3 {
        position: absolute;
        top: 150px;
        left: 10%;
        z-index: 2;
        transition: 2s;
      }
      .img-bg4 {
        position: absolute;
        top: 1px;
        right: 1%;
        z-index: 1;
        transition: 10s;
      }
      .img-name {
        color: #e3d7b6;
        letter-spacing: 1px;

        text-align: center;
        position: absolute;
        right: 70px;
        bottom: 100px;
        z-index: 5;
        transition: 10s;
        h1 {
          font-weight: bold;
        }
      }
    }
  }
  .passion-head {
    color: #fff;
    font-size: 32px;
    text-transform: uppercase;
    font-weight: bolder;
  }
  .item-contain {
    .image {
      img {
        width: 100%;
        border-radius: 10px;
        object-fit: cover;
      }
    }
    .contain {
      font-family: "Roboto", sans-serif !important;

      h5 {
        font-size: 22px;
        color: #fff;
      }

      p {
        font-size: 14px;
        font-weight: 100;
        color: #d0d4da;
        width: 80%;
      }
      .but {
        button {
          border: 1px solid #d0d4da;
          border-radius: 20px;
          font-size: 0.9rem;
          padding: 2px 8px;
          margin-right: 10px;
          color: #fff;
        }
      }
      .price {
        font-size: 25px;
        display: block;
        font-weight: bold;
        color: #fff;
      }
      .add-cart {
        visibility: hidden;
        transition: visibility 1s;
      }
    }

    &:hover .add-cart {
      visibility: visible;
    }
  }
}

@media (min-width: 320px) and (max-width: 767px) {
  .about-image {
    .img-bg {
      height: 400px !important;
      .img-bg1 {
        left: 0;
        top: 0;
        left: 5%;

        img {
          width: 75px;
        }
      }
      .img-bg2 {
        top: 150px;
        left: 5%;
        z-index: 3;
        img {
          width: 150px;
        }
      }
      .img-bg3 {
        top: 180px;
        left: 10%;
        img {
          width: 150px;
        }
      }
      .img-bg4 {
        top: 1px;
        right: 0;
        img {
          width: 250px;
        }
      }
      .img-name {
        color: #e3d7b6;
        letter-spacing: 1px;
        right: 0px;
        top: 280px;
        h4 {
          font-size: 16px;
        }
        h1 {
          font-size: 20px;
          font-weight: bold;
        }
      }
    }
  }
}
</style>
